@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
/* Configuração geral de estilo */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*:focus {
  outline: 0;
}

html,
body,
#root {
  height: 100%;
}
body {
  font: 14px "Roboto", sans-serif;
  background-color: #000;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
button {
  cursor: pointer;
}

.container-center {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login {
  width: 460px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.733);
  border-radius: 10px;
}
.logo-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.logo-area img {
  width: 400px;
  height: 300px;
}
form {
  display: flex;
  width: 70%;
  flex-direction: column;
}
form h1 {
  text-align: center;
  margin-bottom: 0.5em;
}

.login input {
  margin-bottom: 8px;
  height: 30px;
  border: 0;
  border-radius: 7px;
  padding: 15px;
  font-size: 15px;
  background: transparent;
  color: #000;
  border: 1px solid #f6a935;
  letter-spacing: 0.05em;
}
form button {
  height: 30px;
  background: #f6a935;
  border: 1px solid #f6a935;
  margin-bottom: 10px;
  font-size: 15px;
  text-align: center;
  border-radius: 7px;
  color: #fff;
  transition: all 0.4s;
  font-weight: 600;
  letter-spacing: 0.1em;
}
form button:hover {
  color: #000;
  background: transparent;
}
.login a {
  margin-top: 10px;
  color: rgb(5, 0, 0);
  transition: all 0.4s;
  margin-bottom: 15px;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #000;
}
.login a:hover {
  color: #f6a935;
}

@media screen and (max-width: 600px) {
  .login {
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.733);
    border-radius: 10px;
  }
  .logo-area {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 50%;
  }
  .logo-area img {
    width: 300px;
    height: 200px;
  }

  video {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
}


.sideBar {
  margin: 0;
  padding: 0;
  width: 200px;
  background-color: #181c18;
  position: fixed;
  height: 100%;
  overflow: auto;
}

.sideBar div {
  background: url(/static/media/cover.526df814.jpg);
  background-color: #181c18;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 150px;
  padding-top: 30px;
}
.sideBar div img {
  border-radius: 50%;
  display: block;
  margin: auto;
  height: 115px;
  width: 115px;
  -webkit-filter: drop-shadow(2px 3px 6px #121212);
  filter: drop-shadow(2px 3px 6px #121212);
  object-fit: cover;
}
.sideBar a {
  display: block;
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  padding: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  transition: ease-in-out 0.4s;
}
.sideBar a:hover {
  background-color: #121212;
  color: orange;
}
.sideBar a svg {
  margin-right: 0.5em;
}
.content {
  margin-left: 200px;
  padding: 1px 10px;
}
/* Responsividade */

@media screen and (max-width: 700px) {
  .sideBar {
    width: 100%;
    height: auto;
    position: relative;
  }

  div.content {
    margin-left: 0;
  }

  .sideBar a {
    float: left;
  }
  .sideBar div {
    display: none;
  }

  .sideBar a svg {
    display: none;
  }
}

@media screen and (max-width: 418px) {
  .sideBar a {
    text-align: center;
    float: none;
    position: relative;
  }
  .sideBar a svg {
    display: none;
  }
}

.title {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 1em;
  border-radius: 5px;
  background-color: #f8f8f8;
  padding: 0.5em;
}
.title span {
  margin-left: 1em;
  font-size: 1em;
}

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 99; /*Dizendo que vai ficar por cima de todo obj*/
}
.modal .container {
  position: fixed;
  max-width: 600px;
  top: 15%;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 4em 2em;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
}

.close {
  background-color: #f65835;
  border: 0;
  color: #fff;
  position: absolute;
  top: 15px;
  left: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 15px;
  border-radius: 10px;
}
.close svg {
  margin-right: 5px;
}

.modal h2 {
  margin-bottom: 1.1em;
  font-size: 1.5em;
}

.modal span {
  font-weight: bold;
  font-size: 1em;
  color: #121212;
}
.modal span i {
  font-weight: 400;
  margin-right: 1em;
  padding: 2px 8px;
  border-radius: 5px;
}
.row {
  margin-bottom: 0.8em;
}

.modal p {
  padding-top: 0.5em;
  white-space: pre-wrap;
}

.dashboard {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.dashboard span {
  margin: 2em 0;
  font-weight: 600;
  font-size: 1.2em;
}
.novo {
  float: right;
  margin-bottom: 1.5em;
  background-color: orange;
  color: #fff;
  padding: 0.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 1.1em;
  border-radius: 6px;
  transition: ease-in 0.2s;
}
.novo svg {
  margin-right: 5px;
}
.novo:hover {
  background-color: rgb(172, 114, 7);
  transform: scale(1.1);
}

table {
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: fixed;
}
table caption {
  font-size: 1.5em;
  margin: 0.5em 0 0.75em;
}
table tr {
  background-color: #f8f8f8;

  padding: 0.35em;
}
table th,
table td {
  padding: 0.62em;
  text-align: center;
}

table th {
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

table td .action {
  border: 0;
  padding: 5px;
  margin-right: 2px;
  align-items: center;
  display: inline-block;
  border-radius: 4px;
}
table td .action svg {
  vertical-align: middle;
}

table td .badge {
  padding: 4px;
  border-radius: 3px;
  color: #fff;
}
.btn-mais {
  width: 130px;
  padding: 9px 15px;
  background-color: orange;
  color: #fff;
  border: 1px solid #f6a935;
  border-radius: 7px;
  font-size: 1.2em;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s;
  font-weight: 500;
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-top: 10px;
}
.logout-deslogar {
  background-color: rgb(255, 0, 0);
  color: #fff;
  margin-top: 300px;
  width: 90px;
  padding: 6px 15px;
  border-radius: 7px;
  font-size: 1.2em;
  display: flex;
  transition: all 0.4s;
  font-weight: 500;
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  border: none;
}
.logout-deslogar:hover {
  background: rgba(236, 7, 7, 0.568);
  color: #fff;
}
.btn-mais:hover {
  background: #f6a935;
  color: #fff;
}

/* Responsividade */
@media screen and (max-width: 600px) {
  table {
    border: 0;
  }
  table caption {
    font-size: 1.3em;
  }
  table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  table tr {
    border-bottom: 3px solid #ddd;
    display: block;
    margin-bottom: 0.65em;
  }
  table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: 0.8em;
    text-align: right;
  }
  table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }
  table td:last-child {
    border-bottom: 0;
  }

  .logout-deslogar {
    background-color: rgb(255, 0, 0);
    color: #fff;
    margin-top: 140px;
    margin-bottom: 20px;
    width: 90px;
    padding: 6px 15px;
    border-radius: 7px;
    font-size: 1.2em;
    display: flex;
    transition: all 0.4s;
    font-weight: 500;
    font-size: 0.85em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    border: none;
  }
  .logout-deslogar:hover {
    background: rgba(236, 7, 7, 0.568);
    color: #fff;
  }
}

.container {
  display: flex;
  background-color: #f8f8f8;
  border-radius: 5px;
  padding: 0.8em;
  align-items: center;
  margin-bottom: 1em;
}
.profile {
  width: 400px;
}
.label-avatar {
  width: 280px;
  height: 280px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}

.label-avatar input {
  display: none;
}
.label-avatar span {
  z-index: 99;
  position: absolute;
  opacity: 0.7;
  transition: all 0.2s;
}

.label-avatar span:hover {
  opacity: 1;
  transform: scale(1.5); /* fazendo animação aumentando a img de tamanho*/
}

.form-profile img {
  margin-bottom: 1em;
  border-radius: 50%;
  object-fit: cover;
}
.form-profile label {
  margin-bottom: 0.5em;
  font-size: 1.3em;
  letter-spacing: 0.1em;
}
.form-profile .title {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  margin-bottom: 15px;
  letter-spacing: 0.1em;
}

.form-profile input,
textarea,
select {
  /* width: 400px; */
  margin-bottom: 1em;
  padding: 0.5em;
  border: 0;
  border-radius: 5px;
  font-size: medium;
}
.form-profile input:disabled {
  cursor: not-allowed;
}
.form-profile input {
  border: 0.2px solid #f6a935;
}
.form-profile .btn-salvar {
  width: 400px;
  height: 35px;
  margin-top: 1px;
  background: #f6a935;
  transition: all 0.4s;
  color: #fff;
  font-size: 1.1em;
  letter-spacing: 0.1em;
  font-size: 0.85em;
  letter-spacing: 0.08em;
  text-transform: uppercase;
}
.form-profile button:hover {
  background: #2b1a01;
  color: #fff;
}

.logout-btn {
  width: 110px;
  height: 30px;
  padding: 6px 20px;
  background-color: rgb(255, 0, 0);
  color: #fff;
  border: none;
  border-radius: 7px;
  font-size: 1.2em;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s;
  font-weight: 500;
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}
.logout-btn:hover {
  background: rgba(70, 4, 4, 0.986);
  color: #fff;
}

@media screen and (max-width: 500px) {
  .form-profile input,
  textarea,
  select {
    width: 350px;
  }
  .form-profile .btn-salvar {
    width: 320px;
  }
}
@media screen and (max-width: 400px) {
  .form-profile input,
  textarea,
  select {
    width: 310px;
  }
  .form-profile .btn-salvar {
    width: 270px;
  }
}

.tipo {
  font-size: 1.6em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  padding-bottom: 15px;
}
.form-profile .cliente {
  width: 400px;
}
.form-profile .btn-cadastrar {
  width: 400px;
  height: 35px;
  font-size: 1.1em;
  letter-spacing: 0.08em;
  padding: 5px;
  background-color: orange;
  color: #fff;
}

@media screen and (max-width: 500px) {
  .form-profile .cliente {
    width: 350px;
  }
  .form-profile .btn-cadastrar {
    width: 320px;
  }
}
@media screen and (max-width: 400px) {
  .form-profile .cliente {
    width: 320px;
  }
  .form-profile .btn-cadastrar {
    width: 300px;
  }
}

.container select {
  width: 400px;
}
form select {
  padding: 10px;
}
form textarea {
  width: 400px;
  height: 50px;
  resize: none;
  font-size: 1em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}
form .complemento {
  height: 50px;
}
form input[type="radio"] {
  margin-bottom: 24px;
}
form input[type="radio"]:not(:first-child) {
  margin-left: 15px;
}

.placa {
  width: 118px;
  margin-bottom: 30px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}
form .auto {
  width: 250px;
  letter-spacing: 0.1em;
}
.status span {
  padding-left: 0.5em;
  font-size: 1.3em;
}
.cadastrar {
  width: 400px;
  height: 35px;
  background-color: orange;
  color: #fff;
  font-size: 0.85em;
  letter-spacing: 0.08em;
  text-transform: uppercase;
}

@media screen and (max-width: 500px) {
  .container select {
    width: 300px;
  }
  form input[type="radio"] {
  }
  .status span {
    font-size: 13px;
    margin-right: 10px;
    vertical-align: middle;
    font-size: 1.2em;
  }
  .form-profile .status {
    vertical-align: middle;
  }

  form .complemento {
    padding-top: 3px;
  }
  .form-profile .placa {
    width: 110px;
  }
  .form-profile .auto {
    width: 200px;
  }
  .cadastrar {
    width: 250px;
  }
  form textarea {
    width: 350px;
    height: 50px;
    resize: none;
    font-size: 1em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }
}

@media screen and (max-width: 400px) {
  .content {
    box-sizing: border-box;
  }
  form textarea {
    width: 320px;
    height: 50px;
    resize: none;
    font-size: 1em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }
  .form-profile .placa {
    width: 110px;
  }
  .form-profile .auto {
    width: 200px;
  }
  .cadastrar {
    width: 230px;
  }
  .form-profile span {
    margin-left: 3px;
  }
  .status {
    display: flexbox;
  }
}

